@mixin sidebar-image($display-small: false) {
  @if $display-small == false {
    display: none;
  }
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  bottom: unit(-2);
  left: unit(-1);
  position: absolute;
  right: unit(-1);
  top: unit(0);

  @include respond-to('medium-large') {
    bottom: unit(-2);
    display: block;
    left: unit(1);
    top: unit(-2);
  }
}
