/*
 * featured country
 */

.home-featured-country__main {
  font-size: 1.4rem;
  padding-bottom: unit(1);

  @include respond-to('medium-large') {
    padding: 0 unit(2);
  }
}

.home-featured-country__heading {
  margin: unit(2) 0 unit(-0.5);
}

.home-featured-country__heading__sub {
  display: block;
  font: 1.6rem/1 $font-family--kapra-expanded;
  margin-bottom: unit(0.3);
  opacity: 0.8;
  text-transform: uppercase;
}

.home-featured-country__heading__main {
  display: block;
  font: $font-weight--normal 2rem $font-family--sans;
  text-transform: none;

  @include respond-to('medium') {
    font-size: 2.4rem;
  }
}

.home-featured-country__more {
  @include arrow-link('dark-orange');
  color: darken($color--rose-of-sharon, 3);
}

.home-featured-country__sidebar {
  height: 40vw;
  position: relative;

  @include respond-to('medium-large') {
    height: auto;
  }
}

.home-featured-country__sidebar__image {
  @include sidebar-image(true);
}

/*
 * partnerships/get involved
 */

.home-partnerships-involved {
  padding-bottom: 0;
  text-align: center;

  &::after {
    @include pseudo($pos: static);
    background: url(#{$path}/images/home-partnerships-involved.jpg) no-repeat
      50% 100%;
    background-size: cover;
    height: 66.6667vw;
    margin-left: unit(-1);
    margin-top: -10vw;
    width: calc(100% + #{unit(2)});

    @media (min-resolution: 120dpi) and (max-width: map-get($breakpoints, 'small--down')) {
      background-image: url(#{$path}/images/home-partnerships-involved@2x.jpg);
    }

    @include respond-to('small') {
      background-image: url(#{$path}/images/home-partnerships-involved--small.jpg);
    }

    @media (min-resolution: 120dpi) and (min-width: map-get($breakpoints, 'small')) {
      background-image: url(#{$path}/images/home-partnerships-involved--small@2x.jpg);
    }

    @include respond-to('medium') {
      background-image: url(#{$path}/images/home-partnerships-involved--medium.jpg);
    }

    @media (min-resolution: 120dpi) and (min-width: map-get($breakpoints, 'medium')) {
      background-image: url(#{$path}/images/home-partnerships-involved--medium@2x.jpg);
    }

    @include respond-to('medium-large') {
      background-image: url(#{$path}/images/home-partnerships-involved--medium-large.jpg);
    }

    @media (min-resolution: 120dpi) and (min-width: map-get($breakpoints, 'medium-large')) {
      background-image: url(#{$path}/images/home-partnerships-involved--medium-large@2x.jpg);
    }

    @include respond-to('large') {
      background-image: url(#{$path}/images/home-partnerships-involved--large.jpg);
    }

    @media (min-resolution: 120dpi) and (min-width: map-get($breakpoints, 'large')) {
      background-image: url(#{$path}/images/home-partnerships-involved--large@2x.jpg);
    }

    @include respond-to('x-large') {
      background-image: url(#{$path}/images/home-partnerships-involved--x-large.jpg);
    }

    @media (min-resolution: 120dpi) and (min-width: map-get($breakpoints, 'x-large')) {
      background-image: url(#{$path}/images/home-partnerships-involved--x-large@2x.jpg);
    }

    @media (min-width: 107.5em) {
      height: 114.6rem;
      margin-top: -17.2rem;
    }
  }
}

.home-involved__list {
  @include reset-list;
}

/*
 * social
 */

.home-social {
  [class*='heading-'] {
    text-align: center;
  }
}

.home-social__container {
  @include respond-to('medium-large') {
    align-items: center;
    display: flex;
  }
}

.home-social__tweet {
  @include null-margins;

  @include respond-to('medium-large') {
    width: percentage(4.5/10);
  }

  .twitter-tweet {
    margin: 0 auto !important;
  }
}

.home-social__tweet__timeline {
  margin: 0 auto !important;
  max-width: 52rem;
}

.home-social__links {
  @include null-margins;
  padding-top: unit(1.5);
  text-align: center;

  @include respond-to('medium-large') {
    padding-top: 0;
    width: percentage(6.5/10);
  }
}

.home-social__links__list {
  @include reset-list;
  display: inline-block;
  width: auto;

  @include respond-to('medium-large') {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
  }
}

.home-social__links__item {
  &:nth-child(n + 2) {
    margin-top: unit(2.2);
  }

  @include respond-to('medium-large') {
    margin: 0;
    width: 50%;

    &:nth-child(2) {
      margin-top: 0;
    }

    &:nth-child(n + 3) {
      margin-top: unit(3);
    }
  }

  a {
    @include hover-underline;
    align-items: center;
    display: flex;
    font-size: 1.6rem;

    @include respond-to('medium-large') {
      display: block;
      font-size: 2rem;
      padding-top: 20px;
      position: relative;
      text-align: center;

      &::before {
        @include pseudo;
        border: 2px solid $color--rose-of-sharon;
        border-radius: 50%;
        height: 76px;
        left: 50%;
        margin-left: -38px;
        top: 0;
        width: 76px;
      }
    }

    @include respond-to('large') {
      font-size: 2.4rem;
    }
  }

  img {
    height: auto;
    margin-right: unit(0.5);
    width: 18px;

    @include respond-to('medium-large') {
      display: block;
      margin: 0 auto unit(1.2);
      width: 36px;
    }
  }
}
